"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { useToast } from "@/components/ui/use-toast";
import { createClient } from "@/utils/supabase/client";

export function PromptSettingsForm() {
  const [systemPrompt, setSystemPrompt] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();
  const supabase = createClient();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const { error } = await supabase.from("settings").upsert({
        key: "openai_system_prompt",
        value: systemPrompt,
      });

      if (error) throw error;

      toast({
        title: "Success",
        description: "Prompt settings have been saved successfully.",
      });
    } catch (error) {
      console.error("Error saving prompt settings:", error);
      toast({
        title: "Error",
        description: "Failed to save prompt settings. Please try again.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="space-y-2">
        <Label htmlFor="systemPrompt">System Prompt</Label>
        <Textarea
          id="systemPrompt"
          value={systemPrompt}
          onChange={(e) => setSystemPrompt(e.target.value)}
          placeholder="Enter your OpenAI system prompt here..."
          className="min-h-[200px]"
        />
        <p className="text-sm text-muted-foreground">
          This prompt will be used as the system message for all OpenAI API
          calls.
        </p>
      </div>
      <Button type="submit" disabled={isLoading}>
        {isLoading ? "Saving..." : "Save Settings"}
      </Button>
    </form>
  );
}
